.shealth-shortcode-wrap {
  border: 2px solid #CCC;
  padding: 15px;

  &.shealth-style-1 {

    .shealth-domain-name {
      display: block;
      width: 100%;
      margin: 0 9px 10px 0;
    }

    .shealth-spinner {
      background: url('../images/spinner.gif') no-repeat;
      background-size: 16px 16px;
      float: right;
      display: none;
      width: 16px;
      height: 16px;
      margin: 5px 5px 0;
    }

    .shealth-results {
      display: block;
      width: 100%;
      margin: 5px 0;
    }

  }
}
